import React, { useState } from 'react'
import Logo from '../Logo/Logo'
import HomeIcon from '@mui/icons-material/Home'
import SearchIcon from '@mui/icons-material/Search'
import CloseIcon from '@mui/icons-material/Close'
import PeopleOutlineIcon from '@mui/icons-material/PeopleOutline';
import PostAddIcon from '@mui/icons-material/PostAdd';
import NotificationsIcon from '@mui/icons-material/Notifications';
import SettingsIcon from '@mui/icons-material/Settings';
import me from '../../imgs/me.jpg'
function Navbar()
{
    const [startSearch, setStartSearch] = useState(false)
    const [activePage, setActivePage] = useState('Home')
    const [icons, setIcons] = useState([
        {
            icon: <HomeIcon />,
            title: 'Home'
        },
        {
            icon: <PeopleOutlineIcon />,
            title: 'Friends'
        },
        {
            icon: <PostAddIcon />,
            title: 'Write'
        }
    ])
    return (
        <div className="mainNav">
            <div className="nav-container" >
                <div className=" mainNav__left">
                    <div className="mainNav__left__logo">
                        <Logo />
                    </div>
                    <div className={startSearch ? `mainNav__left__search__active` : `mainNav__left__search`} >
                        <SearchIcon onClick={() => setStartSearch(!startSearch)} />
                        <input type="text" />
                        <CloseIcon className={startSearch ? `mainNav__left__search__active__close` : `mainNav__left__search__close`} onClick={() => setStartSearch(false)} />
                    </div>
                </div>
                <ul className=" mainNav__middle">
                    <div className="row">
                        {icons.map((item, i) => (
                            <li key={i} title={item.title} className={activePage == item.title ? 'col mainNav__middle__link active' : 'col mainNav__middle__link'} onClick={() => setActivePage(item.title)} > {item.icon} </li>
                        ))}
                    </div>
                </ul>
                <div className=" mainNav__right">

                    <div className="mainNav__right__item ">
                        <img src={me} className="mainNav__right__item img__rounded" alt="personal img" />
                    </div>
                    <div className="mainNav__right__item nav-icon ">
                        <NotificationsIcon />
                    </div>
                    <div className="mainNav__right__item nav-icon ">
                        <SettingsIcon />
                    </div>
                </div>
            </div >
        </div >
    )
}

export default Navbar