import React from 'react'
import Post from './Post'
import WritePost from './WritePost'
function Posts()
{
    return (
        <div className='mainPage__middle'>
            <WritePost />
            <div className="mainPage__middle__posts">
                <Post />
                <Post />
                <Post />
                <Post />
            </div>
        </div>
    )
}

export default Posts