import React from 'react'
import me from '../../imgs/me.jpg'
import cover from '../../imgs/cover.jpg'
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import ChatBubbleOutlineIcon from '@mui/icons-material/ChatBubbleOutline';
import Comment from './Comment';

function Post()
{
    return (
        <div className="mainPage__middle__posts__post">
            <div className="mainPage__middle__posts__post__userInfo">
                <div className="mainPage__middle__posts__post__userInfo__img img__rounded">
                    <img src={me} alt="you" />
                </div>
                <div className="mainPage__middle__posts__post__userInfo__name">
                    mohammed ramadan
                </div>
            </div>
            <div className="mainPage__middle__posts__post__content">
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Dignissimos excepturi saepe aliquam sunt consequuntur fugiat nisi impedit, nam recusandae facere a aliquid beatae adipisci tempore! Nobis minima aliquid ullam placeat.
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Dignissimos excepturi saepe aliquam sunt consequuntur fugiat nisi impedit, nam recusandae facere a aliquid beatae adipisci tempore! Nobis minima aliquid ullam placeat.
            </div>
            <div className="mainPage__middle__posts__post__img">
                <img src={cover} alt="post img" />
            </div>
            <div className="mainPage__middle__posts__post__numbers">
                <div className="mainPage__middle__posts__post__numbers__likesNums">0 likes</div>
                <div className="mainPage__middle__posts__post__numbers__commentsNums"> 0 comments </div>
            </div>
            <div className="mainPage__middle__posts__post__actions">
                <div className="mainPage__middle__posts__post__actions__item">
                    <ThumbUpOffAltIcon /> like

                </div>
                <div className="mainPage__middle__posts__post__actions__item">
                    <ChatBubbleOutlineIcon /> comment

                </div>
            </div>
            <div className="mainPage__middle__posts__post__commentsGroupe">
                <div className="mainPage__middle__posts__post__commentsGroupe__writeComment">
                    <div className="mainPage__middle__posts__post__commentsGroupe__writeComment__userImg img__rounded">
                        <img src={me} alt="img" />
                    </div>
                    <div className="mainPage__middle__posts__post__commentsGroupe__writeComment__input">
                        <input type="text" placeholder='write a comment' />
                    </div>
                </div>
                <div className="mainPage__middle__posts__post__commentsGroupe__comments">
                    <Comment />
                    <Comment />
                    <Comment />
                </div>
            </div>
        </div>
    )
}

export default Post